.fadein {
    width: 100%;
    height: 100%;
    background-color: black;
    position: absolute;
    animation: fadeIn 4s ease-out;
    opacity: 0;
    z-index: 999;
    pointer-events: none;
    display: flex;
    .initText {
        color: white;
        animation: typeText 4s ease-out;
        text-align: initial;
        margin: 2rem;
        opacity: 0;
        width: 100%;
    }
}
.bootWrap {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 900;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: inherit;
    background: rgba(138, 245, 66, 0.03);
}
.initButton {
    cursor: pointer;

    margin: 1rem;
}
@keyframes fadeIn {
    0% {
        pointer-events: all;
        cursor: progress;
        opacity: 1;
    }
    25% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes typeText {
    0% {
        opacity: 1;
    }
    49% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
