@import './../../styles.scss';
.window {
    border-top: $border;
    border-right: $heavyBorder;
    border-bottom: $heavyBorder;
    border-left: $border;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    position: absolute;
}
.header {
    border-bottom: $border;
    position: relative;
    display: flex;
    .title {
        margin: 0 0.5rem;
        word-break: initial;
    }
    .close {
        position: absolute;
        margin-bottom: auto;
        margin-top: auto;
        left: 0.6rem;
        top: 0;
        text-align: center;
        bottom: 0;
        width: 0.8rem;
        height: 0.7rem;
        border: 1px black solid;
        background-color: white;
        cursor: pointer;
        &:active {
            background-color: black;
            color: black;
        }
    }
}
.contents {
    overflow-y: scroll;
    overflow-x: scroll;
    resize: both;

    > div {
        margin: 1rem;
        height: calc(100% - 2rem);
    }
}

.lineWrap {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-evenly;
    margin: 0 0.2rem;
    .line {
        border-bottom: 1px black solid;
    }
}
@media only screen and (max-width: 800px) {
    .window {
        position: relative;
        margin-top: -4rem;
        .contents {
            transition: width 0.5s;
            transition: height 0.5s;
            min-width: unset;
            min-height: unset;
            width: 90vw !important;
            height: 80vh !important;
        }
    }
}
